/* eslint-disable @typescript-eslint/no-explicit-any */
import { ShaderMaterialProps } from "@react-three/fiber";
import { useMemo, useRef } from "react";
import {
  mergeUniforms,
  ShaderChunk,
  ShaderMaterial,
  Uniform,
  UniformsLib,
  UniformsUtils,
  PointsMaterial,
  ShaderMaterialParameters,
  Material,
  Color,
} from 'three';
import fragmentShader from './image.frag';
import vertexShader from './circleSection.vert';
import { useRefFrame } from '../../utils/useRefFrame';
import { useRefLayoutEffect } from '../../utils/useRefLayoutEffect';
export interface CellMaterialProps extends ShaderMaterialProps {
  _m?: unknown
}
console.dir([ShaderMaterial, PointsMaterial]);

export function CellMaterial(props: CellMaterialProps) {
  const [shaderDef] = useMemo<[ShaderMaterialParameters]>(() => [{
    defines: {
      USE_SIZEATTENUATION: '',
      USE_MAP:'',
      USE_COLOR:'',
    },
    uniforms: UniformsUtils.merge([
      UniformsLib.common,
      UniformsLib.points,
      UniformsLib.fog,
      { randBase: { value: Math.random() } },
    ]),
    fragmentShader,
    vertexShader,
  }], []);
  const ref = useRef<ShaderMaterial>();
  useRefFrame(([mat], [{ clock }]) => {
    mat.uniforms.randBase.value = Math.sin(Math.random() * 0.01 + mat.uniforms.randBase.value||0) ** 2;
    // mat.uniforms.size.value = 200;
    // mat.uniforms.scale.value = 300;
    return;
    // let { value: rV } = mat.uniforms.randBase;
    // rV = rV || 0;
    // rV += (Math.random() - 0.5) * 0.005;
    // rV *= (rV < 1) as any;
    // rV *= (rV > 0) as any;
    // mat.uniforms.randBase.value = rV;
    // mat.uniformsNeedUpdate = true;
  }, [ref]);
  useRefLayoutEffect(([mat]) => {
    const openMat = mat as any;
    openMat.isMeshBasicMaterial = true;
    openMat.isPointsMaterial = true;
    openMat.color = new Color(0xf8f8f8);
    // mat.uniforms.sizeAttenuation.value = true;
  }, [ref]);
  return (
    <shaderMaterial
      {...props}
      args={[shaderDef]}
      ref={ref}
      // onBeforeCompile={(...args) => {debugger;console.log(args)}}
      >
    </shaderMaterial>
  );
}
