import { useCallback, useLayoutEffect, useRef, useState } from 'react';
import { DoubleSide, LinearFilter, MirroredRepeatWrapping } from 'three';
import { useRefEffect } from '../utils/useRefEffect';
import { useRefLayoutEffect } from '../utils/useRefLayoutEffect';
import type { CanvasTexture } from 'three';
import { useSpring, animated } from '@react-spring/three';

const BLANK_IMG = new ImageData(2, 2);
BLANK_IMG.data.set([
  255, 255, 255, 255,
  0, 0, 0, 255,
  255, 255, 255, 255,
  0, 0, 0, 255,
]);
type ImageMaterialProps = {
  src: string;
  onLoad?(image: ImageData): void;
  delay?: number;
};
const dataCache: Record<string, ImageData> = {};
const DOWNSAMPLING = 1;
export function ImageMaterial({ src, onLoad, delay }: ImageMaterialProps) {
  const tRef = useRef<CanvasTexture>();
  const imgR = useRef(document.createElement('canvas').getContext('2d'));
  const iLoader = useRef(new Image());
  const [iD, setImageData] = useState<ImageData>(dataCache[src] || BLANK_IMG);
  const didLoad = useCallback((i) => {
    onLoad && onLoad(i);
    setImageData(i);
  }, [onLoad]);
  useRefEffect(([ct]) => {
    if (iD) {
      ct.image = iD;
      ct.needsUpdate = true;
      if (iD !== BLANK_IMG) {
        dataCache[src] = iD;
      }
    }
  }, [tRef], [iD]);
  useRefEffect(([context]) => {
    let delOpen: NodeJS.Timeout;
    if (iD === dataCache[src]) {
      delOpen = setTimeout(() => didLoad(iD), delay);

      return () => {
        clearTimeout(delOpen);
      };
    }
    // const context = imgEl.getContext && imgEl.getContext('2d');
    // if (!context) return;
    const iht = iLoader.current;
    iht.onload = () => {
      const { naturalHeight, naturalWidth } = iht;
      const h = naturalHeight * DOWNSAMPLING;
      const w = naturalWidth * DOWNSAMPLING;
      context.canvas.width = w;
      context.canvas.height = h;
      context.fillStyle = '#020202';
      context.fillRect(0, 0, w, h);
      context.drawImage(iht, 0, 0, w, h);
      context.scale(-1, 1);
      const i = context.getImageData(0, 0, w, h);
      // onLoad && onLoad(i);
      delOpen = setTimeout(() => didLoad(i), delay);
      ;
    };
    iht.src = src;
    return () => {
      iht.onload = null;
      clearTimeout(delOpen);
    };
  }, [imgR], [src, didLoad]);
  return (
    <meshPhysicalMaterial
      // flatShading
      // vertexColors
      // wireframe
      // depthTest={false}
      // depthWrite={false}
      clipIntersection
      morphTargets
      // shininess={0.0}
      transparent
      clearcoat={1}
      clearcoatRoughness={0.9}
      fog={false}
      metalness={0.1}
      ref={useRef()}
      reflectivity={0.4}
      roughness={0.4}
      side={DoubleSide}
      >
      <canvasTexture
        flipY
        anisotropy={2}
        attach="map"
        magFilter={LinearFilter}
        minFilter={LinearFilter}
        needsUpdate={false}
        // offset-x={0.2}
        // offset-y={0.2}
        ref={tRef}
        // repeat-x={1.25}
        // repeat-y={1.25}
        // wrapS={MirroredRepeatWrapping}
        // wrapT={MirroredRepeatWrapping}
        />
    </meshPhysicalMaterial>
  );
  // return (
  //   <meshPhysicalMaterial
  //     // flatShading
  //     // vertexColors
  //     // wireframe
  //     // depthTest={false}
  //     // depthWrite={false}
  //     clipIntersection
  //     morphTargets
  //     // shininess={0.0}
  //     transparent
  //     clearcoat={1}
  //     clearcoatRoughness={0.9}
  //     fog={false}
  //     metalness={0.1}
  //     ref={useRef()}
  //     reflectivity={0.4}
  //     roughness={0.4}
  //     side={DoubleSide}
  //     >
  //     <canvasTexture
  //       flipY
  //       anisotropy={2}
  //       attach="map"
  //       magFilter={LinearFilter}
  //       minFilter={LinearFilter}
  //       needsUpdate={false}
  //       // offset-x={0.2}
  //       // offset-y={0.2}
  //       ref={tRef}
  //       // repeat-x={1.25}
  //       // repeat-y={1.25}
  //       wrapS={MirroredRepeatWrapping}
  //       wrapT={MirroredRepeatWrapping}
  //       />
  //   </meshPhysicalMaterial>
  // );
}
