export interface IGridCell<T> {
  readonly key: T;
  height: number;
  col?: IGridCol<T>;
  colHeight?: number;
  next: CompleteGridCell<T> | undefined;
}
export type CompleteGridCell<T> = Required<IGridCell<T>>;
export type PartialGridCell<T> = IGridCell<T> & { next: undefined; };
export interface IGridCol<T> {
  readonly index: number;
  head?: CompleteGridCell<T>;
  heightSum: number;
  remove(cell: CompleteGridCell<T>): unknown;
}

export enum GridConstraints {
  COL_WIDTH,
  COL_HEIGHT,
  MAX_COLS,
  MIN_CELL_HEIGHT,
}
export type ColConstraint = [unitConstraint: number, maxConstraint?: number];
export type PositionedCell<T> = {
  column: number;
  rect: [x: number, y: number, width: number, height: number];
  cell: Required<IGridCell<T>>;
};
