import { pluck } from "../pluck";
import { IGridCol, CompleteGridCell, IGridCell, PartialGridCell } from "./IGridCell";


export class GridCol<T> implements IGridCol<T> {
  heightSum = 0;

  head?: CompleteGridCell<T>;

  constructor(public index: number) { }

  static ColumnMismatchError = class ColumnMismatchError extends Error {
    name = 'ColumnMismatchError';
  };
  static ColumnCircularError = class ColumnCircularError extends Error {
    name = 'ColumnCircularError';
  };

  static assertCellsEqual<T>(cell: IGridCell<T>, existing: CompleteGridCell<T> | undefined) {
    if (!existing)
      return;
    if (!cell.col)
      return;
    if (!cell.next)
      return;
    if (existing.col !== cell.col)
      throw new this.ColumnMismatchError();
  }

  static divorceCell<T>(cell: IGridCell<T>) {
    delete cell.col;
    delete cell.colHeight;
    cell.next = undefined;
  }

  isEmpty(): this is { head: undefined; } {
    return typeof this.head === 'undefined';
  }
  remove(cell: IGridCell<T>): asserts cell is PartialGridCell<T> {
    if (this.isEmpty())
      return;
    this.heightSum -= cell.height;
    let prevCell = this.head;
    const nextCell = cell.next;
    Array.from(this).forEach(cel => {
      if (cel.key === cell.key) {
        if (prevCell){
          prevCell.next = nextCell;
          delete cell.col;
          cell.next = undefined;
        }
      } else {
        cel.colHeight = this.heightSum;
      }
      prevCell = cel;
    });
    this.#cache.delete(cell.key);
  }

  clear() {
    this.heightSum = 0;
    this.head = undefined;
    for (const cell of this.#cache.values()) {
      GridCol.divorceCell(cell);
    }
    this.#cache.clear();
  }

  static clearCol(col: GridCol<unknown>) {
    col.clear();
  }

  cloneFrom(otherCol: GridCol<T>) {
    if (this.index !== otherCol.index) {
      console.warn('cannot clone different columns');
    } else {
      this.heightSum = otherCol.heightSum;
      let current = otherCol.head;
      this.head = current;
      while(current) {
        this.#cache.set(current.key, current);
        current.col = this;
        current = current.next;
      }
    }
  }

  #cache = new Map<T, CompleteGridCell<T>>();

  add(cell: IGridCell<T>) {
    const existing = this.#cache.get(cell.key);
    const heightDelta = cell.height - (existing?.height || 0);
    this.heightSum += heightDelta;
    GridCol.assertCellsEqual(cell, existing);
    let current = this.head;
    let last;
    this.checkCells();
    while (current) {
      current.colHeight = this.heightSum;
      last = current;
      current = current.next;
    }
    if (existing)
      return;
    cell.col = this;
    cell.colHeight = this.heightSum;
    if (last)
      last.next = cell as CompleteGridCell<T>;
    else
      this.head = cell as CompleteGridCell<T>;
    this.#cache.set(cell.key, cell as CompleteGridCell<T>);
  }

  private checkCells() {
    const cellArray = Array.from(this.#cache.values());
    if (cellArray.length && cellArray.every(pluck('next'))) {
      throw new GridCol.ColumnCircularError();
    }
  }
  *[Symbol.iterator]() {
    let current = this.head;
    this.checkCells();
    while (current) {
      yield current;
      current = current.next;
    }
  }
}
